<script>
  import store from "../store/index.js"
  import { validationMixin } from "vuelidate";
  import { required, minLength, maxLength, alphaNum, email, numeric, sameAs, password} from "vuelidate/lib/validators";
  export default {
    name: 'Home',
    data() {
      return {
        alertCountdown: 0,
        alertType: '',
        alertText: '',
        addLessonModal: false,
        form: {
          lesson: null,
          lessonURL: null,
          date: null,
          time: null,
          description: null
        },
      }
    },
    validations: {
      form: {
        lesson: {
          required
        },
        lessonURL: {
          required
        },
        date: {
          required
        },
        time: {
          required
        },
        description: {
          required
        }
      }
    },
    methods: {
      addLessonFunction() {
        this.addLesson();
      },
      addLesson() {
        this.$v.form.$touch();
        if (this.$v.form.$anyError) {
          return;
        }
        this.addData();
      },
      addData() {
        this.alertType = "success"
        this.alertText = "    "
        this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        this.resetForm();
      },
      resetForm() {
        this.form = {
          lesson: null,
          lessonURL: null,
          date: null,
          time: null,
          description: null,
        };
        this.$nextTick(() => {
          this.$v.$reset();
        });
        this.addLessonModal = false;
      },
      validateState(name) {
        const { $dirty, $error } = this.$v.form[name];
        return $dirty ? !$error : null;
      },
      countDownChanged(alertCountdown) {
        this.alertCountdown = alertCountdown
      },
      openAddLessonModal() {
        this.addLessonModal = true;
      }
    },
    created() {
      if(store.getters.authToken == null) {
        store.commit("SET_TOKEN", null);
        store.commit("SET_USERNAME", null);
        this.$router.push({
          name: 'login'
        })
      }
    }
  }
</script>
